"use client";

import { useState, useRef } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface LocationData {
  lat: number;
  lng: number;
  name?: string;
  polygon?: Array<[number, number]>;
}

interface UploadKMLProps {
  onLocationSelect: (location: LocationData) => void;
}

export default function UploadKML({ onLocationSelect }: UploadKMLProps) {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [fileContent, setFileContent] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ŸÜŸàÿπ ÿßŸÑŸÖŸÑŸÅ
    const allowedTypes = ['.kml', '.kmz'];
    const fileExtension = file.name.toLowerCase().substring(file.name.lastIndexOf('.'));
    
    if (!allowedTypes.includes(fileExtension)) {
      setError("Ÿäÿ±ÿ¨Ÿâ ÿßÿÆÿ™Ÿäÿßÿ± ŸÖŸÑŸÅ KML ÿ£Ÿà KMZ ŸÅŸÇÿ∑");
      return;
    }

    // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ≠ÿ¨ŸÖ ÿßŸÑŸÖŸÑŸÅ (5MB ŸÉÿ≠ÿØ ÿ£ŸÇÿµŸâ)
    if (file.size > 5 * 1024 * 1024) {
      setError("ÿ≠ÿ¨ŸÖ ÿßŸÑŸÖŸÑŸÅ Ÿäÿ¨ÿ® ÿ£ŸÜ ŸäŸÉŸàŸÜ ÿ£ŸÇŸÑ ŸÖŸÜ 5 ŸÖŸäÿ¨ÿßÿ®ÿßŸäÿ™");
      return;
    }

    setSelectedFile(file);
    setError(null);
  };

  const parseKMLContent = (content: string): LocationData | null => {
    try {
      // ÿßŸÑÿ®ÿ≠ÿ´ ÿπŸÜ ÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ ŸÅŸä ŸÖÿ≠ÿ™ŸàŸâ KML
      const coordinatesMatch = content.match(/<coordinates>([\s\S]*?)<\/coordinates>/);
      if (!coordinatesMatch) {
        throw new Error("ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ ÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ ŸÅŸä ÿßŸÑŸÖŸÑŸÅ");
      }

      const coordinatesText = coordinatesMatch[1].trim();
      const coordinates = coordinatesText.split(/\s+/).map(coord => {
        const parts = coord.split(',');
        return [parseFloat(parts[1]), parseFloat(parts[0])]; // lat, lng
      }).filter(coord => !isNaN(coord[0]) && !isNaN(coord[1]));

      if (coordinates.length === 0) {
        throw new Error("ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ ÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ ÿµÿ≠Ÿäÿ≠ÿ©");
      }

      // ÿßŸÑÿ®ÿ≠ÿ´ ÿπŸÜ ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÉÿßŸÜ
      const nameMatch = content.match(/<name>(.*?)<\/name>/);
      const placeName = nameMatch ? nameMatch[1] : "ŸÖŸàŸÇÿπ ŸÖŸÜ ŸÖŸÑŸÅ KML";

      if (coordinates.length === 1) {
        // ŸÜŸÇÿ∑ÿ© Ÿàÿßÿ≠ÿØÿ©
        return {
          lat: coordinates[0][0],
          lng: coordinates[0][1],
          name: placeName
        };
      } else {
        // ŸÖÿ∂ŸÑÿπ
        const centerLat = coordinates.reduce((sum, coord) => sum + coord[0], 0) / coordinates.length;
        const centerLng = coordinates.reduce((sum, coord) => sum + coord[1], 0) / coordinates.length;
        
        return {
          lat: centerLat,
          lng: centerLng,
          name: placeName,
          polygon: coordinates as Array<[number, number]>
        };
      }
    } catch (err) {
      throw new Error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÑŸäŸÑ ŸÖÿ≠ÿ™ŸàŸâ ÿßŸÑŸÖŸÑŸÅ");
    }
  };

  const handleFileUpload = async () => {
    if (!selectedFile) return;

    setLoading(true);
    setError(null);

    try {
      const reader = new FileReader();
      
      reader.onload = async (e) => {
        try {
          let content = e.target?.result as string;
          
          if (selectedFile.name.toLowerCase().endsWith('.kmz')) {
            // ŸÑŸÑŸÖŸÑŸÅÿßÿ™ ÿßŸÑŸÖÿ∂ÿ∫Ÿàÿ∑ÿ© KMZÿå ŸÜÿ≠ÿ™ÿßÿ¨ ŸÑÿßÿ≥ÿ™ÿÆÿ±ÿßÿ¨ ŸÖÿ≠ÿ™ŸàŸâ KML
            // Ÿáÿ∞ÿß ŸÖÿ´ÿßŸÑ ŸÖÿ®ÿ≥ÿ∑ - ŸÅŸä ÿßŸÑÿ™ÿ∑ÿ®ŸäŸÇ ÿßŸÑÿ≠ŸÇŸäŸÇŸä ŸÜÿ≠ÿ™ÿßÿ¨ ŸÖŸÉÿ™ÿ®ÿ© ŸÑŸÅŸÉ ÿßŸÑÿ∂ÿ∫ÿ∑
            setError("ŸÖŸÑŸÅÿßÿ™ KMZ ÿ∫Ÿäÿ± ŸÖÿØÿπŸàŸÖÿ© ÿ≠ÿßŸÑŸäÿßŸã. Ÿäÿ±ÿ¨Ÿâ ÿßÿ≥ÿ™ÿÆÿØÿßŸÖ ŸÖŸÑŸÅ KML");
            setLoading(false);
            return;
          }

          setFileContent(content);
          const locationData = parseKMLContent(content);
          
          if (locationData) {
            onLocationSelect(locationData);
          }
        } catch (err) {
          setError(err instanceof Error ? err.message : "ÿÆÿ∑ÿ£ ŸÅŸä ŸÇÿ±ÿßÿ°ÿ© ÿßŸÑŸÖŸÑŸÅ");
        } finally {
          setLoading(false);
        }
      };

      reader.onerror = () => {
        setError("ÿÆÿ∑ÿ£ ŸÅŸä ŸÇÿ±ÿßÿ°ÿ© ÿßŸÑŸÖŸÑŸÅ");
        setLoading(false);
      };

      reader.readAsText(selectedFile);
    } catch (err) {
      setError("ÿÆÿ∑ÿ£ ŸÅŸä ŸÖÿπÿßŸÑÿ¨ÿ© ÿßŸÑŸÖŸÑŸÅ");
      setLoading(false);
    }
  };

  const clearFile = () => {
    setSelectedFile(null);
    setFileContent(null);
    setError(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  return (
    <Card>
      <CardContent className="p-6">
        <div className="space-y-4">
          {/* ŸÖŸÜÿ∑ŸÇÿ© ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ */}
          <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center hover:border-gray-400 transition-colors">
            <div className="text-4xl mb-4">üìÅ</div>
            <h3 className="text-lg font-semibold mb-2">ÿ±ŸÅÿπ ŸÖŸÑŸÅ KML/KMZ</h3>
            <p className="text-gray-600 mb-4">
              ÿßÿ≥ÿ≠ÿ® ÿßŸÑŸÖŸÑŸÅ ŸáŸÜÿß ÿ£Ÿà ÿßŸÜŸÇÿ± ŸÑŸÑÿßÿÆÿ™Ÿäÿßÿ±
            </p>
            
            <input
              ref={fileInputRef}
              type="file"
              accept=".kml,.kmz"
              onChange={handleFileSelect}
              className="hidden"
            />
            
            <Button
              variant="outline"
              onClick={() => fileInputRef.current?.click()}
              disabled={loading}
            >
              ÿßÿÆÿ™Ÿäÿßÿ± ŸÖŸÑŸÅ
            </Button>
          </div>

          {/* ŸÖÿπŸÑŸàŸÖÿßÿ™ ÿßŸÑŸÖŸÑŸÅ ÿßŸÑŸÖÿ≠ÿØÿØ */}
          {selectedFile && (
            <div className="bg-blue-50 p-4 rounded-lg">
              <div className="flex justify-between items-center">
                <div className="text-right">
                  <p className="font-medium text-blue-900">{selectedFile.name}</p>
                  <p className="text-sm text-blue-700">
                    ÿßŸÑÿ≠ÿ¨ŸÖ: {(selectedFile.size / 1024).toFixed(1)} ŸÉŸäŸÑŸàÿ®ÿßŸäÿ™
                  </p>
                </div>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={clearFile}
                  disabled={loading}
                >
                  ÿ•ÿ≤ÿßŸÑÿ©
                </Button>
              </div>
            </div>
          )}

          {/* ÿ±ÿ≥ÿßÿ¶ŸÑ ÿßŸÑÿÆÿ∑ÿ£ */}
          {error && (
            <Alert className="border-red-200 bg-red-50">
              <AlertDescription className="text-right text-red-700">
                {error}
              </AlertDescription>
            </Alert>
          )}

          {/* ŸÖÿπÿßŸäŸÜÿ© ŸÖÿ≠ÿ™ŸàŸâ ÿßŸÑŸÖŸÑŸÅ */}
          {fileContent && (
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-medium mb-2 text-right">ŸÖÿπÿßŸäŸÜÿ© ŸÖÿ≠ÿ™ŸàŸâ ÿßŸÑŸÖŸÑŸÅ:</h4>
              <div className="text-sm text-gray-600 max-h-32 overflow-y-auto">
                <pre className="whitespace-pre-wrap text-xs">
                  {fileContent.substring(0, 500)}
                  {fileContent.length > 500 && '...'}
                </pre>
              </div>
            </div>
          )}

          {/* ÿ≤ÿ± ÿßŸÑŸÖÿπÿßŸÑÿ¨ÿ© */}
          {selectedFile && (
            <Button
              onClick={handleFileUpload}
              disabled={loading}
              className="w-full"
            >
              {loading ? (
                <div className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                  ÿ¨ÿßÿ±Ÿä ŸÖÿπÿßŸÑÿ¨ÿ© ÿßŸÑŸÖŸÑŸÅ...
                </div>
              ) : (
                "ŸÖÿπÿßŸÑÿ¨ÿ© ÿßŸÑŸÖŸÑŸÅ Ÿàÿßÿ≥ÿ™ÿÆÿ±ÿßÿ¨ ÿßŸÑÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™"
              )}
            </Button>
          )}

          {/* ÿ™ÿπŸÑŸäŸÖÿßÿ™ */}
          <div className="text-sm text-gray-500 text-right space-y-1">
            <p><strong>ÿßŸÑŸÖŸÑŸÅÿßÿ™ ÿßŸÑŸÖÿØÿπŸàŸÖÿ©:</strong> KML (.kml)</p>
            <p><strong>ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ŸÇÿµŸâ ŸÑŸÑÿ≠ÿ¨ŸÖ:</strong> 5 ŸÖŸäÿ¨ÿßÿ®ÿßŸäÿ™</p>
            <p><strong>ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ ÿßŸÑŸÖÿØÿπŸàŸÖ:</strong> ÿßŸÑŸÜŸÇÿßÿ∑ ŸàÿßŸÑŸÖÿ∂ŸÑÿπÿßÿ™</p>
          </div>

          {/* ŸÖÿ´ÿßŸÑ ÿπŸÑŸâ ÿ™ŸÜÿ≥ŸäŸÇ KML */}
          <details className="text-sm">
            <summary className="cursor-pointer text-blue-600 hover:text-blue-800">
              ŸÖÿ´ÿßŸÑ ÿπŸÑŸâ ÿ™ŸÜÿ≥ŸäŸÇ KML ÿßŸÑŸÖÿØÿπŸàŸÖ
            </summary>
            <div className="mt-2 p-3 bg-gray-100 rounded text-xs">
              <pre>{`<?xml version="1.0" encoding="UTF-8"?>
<kml xmlns="http://www.opengis.net/kml/2.2">
  <Document>
    <Placemark>
      <name>ŸÖŸàŸÇÿπ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿä</name>
      <Point>
        <coordinates>46.6753,24.7136,0</coordinates>
      </Point>
    </Placemark>
  </Document>
</kml>`}</pre>
            </div>
          </details>
        </div>
      </CardContent>
    </Card>
  );
}
