"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface LocationData {
  lat: number;
  lng: number;
  name?: string;
}

interface DataInputProps {
  onLocationSelect: (location: LocationData) => void;
  coordinatesMode?: boolean;
}

export default function DataInput({ onLocationSelect, coordinatesMode = false }: DataInputProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const [latitude, setLatitude] = useState("");
  const [longitude, setLongitude] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSearch = async () => {
    if (!searchQuery.trim()) {
      setError("Ÿäÿ±ÿ¨Ÿâ ÿ•ÿØÿÆÿßŸÑ ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÉÿßŸÜ");
      return;
    }

    setLoading(true);
    setError(null);

    try {
      // ÿßÿ≥ÿ™ÿÆÿØÿßŸÖ Nominatim API ŸÑŸÑÿ®ÿ≠ÿ´ ÿßŸÑÿ¨ÿ∫ÿ±ÿßŸÅŸä
      const response = await fetch(
        `https://nominatim.openstreetmap.org/search?q=${encodeURIComponent(searchQuery)}&format=json&limit=1&accept-language=ar`
      );
      
      if (!response.ok) {
        throw new Error("ŸÅÿ¥ŸÑ ŸÅŸä ÿßŸÑÿßÿ™ÿµÿßŸÑ ÿ®ÿÆÿØŸÖÿ© ÿßŸÑÿ®ÿ≠ÿ´");
      }

      const data = await response.json();
      
      if (data.length === 0) {
        setError("ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ ÿßŸÑŸÖŸàŸÇÿπ ÿßŸÑŸÖÿ∑ŸÑŸàÿ®");
        return;
      }

      const result = data[0];
      const location: LocationData = {
        lat: parseFloat(result.lat),
        lng: parseFloat(result.lon),
        name: result.display_name
      };

      onLocationSelect(location);
    } catch (err) {
      setError("ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ŸÅŸä ÿßŸÑÿ®ÿ≠ÿ´. Ÿäÿ±ÿ¨Ÿâ ÿßŸÑŸÖÿ≠ÿßŸàŸÑÿ© ŸÖÿ±ÿ© ÿ£ÿÆÿ±Ÿâ.");
    } finally {
      setLoading(false);
    }
  };

  const handleCoordinatesSubmit = () => {
    const lat = parseFloat(latitude);
    const lng = parseFloat(longitude);

    if (isNaN(lat) || isNaN(lng)) {
      setError("Ÿäÿ±ÿ¨Ÿâ ÿ•ÿØÿÆÿßŸÑ ÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ ÿµÿ≠Ÿäÿ≠ÿ©");
      return;
    }

    if (lat < -90 || lat > 90) {
      setError("ÿÆÿ∑ ÿßŸÑÿπÿ±ÿ∂ Ÿäÿ¨ÿ® ÿ£ŸÜ ŸäŸÉŸàŸÜ ÿ®ŸäŸÜ -90 Ÿà 90");
      return;
    }

    if (lng < -180 || lng > 180) {
      setError("ÿÆÿ∑ ÿßŸÑÿ∑ŸàŸÑ Ÿäÿ¨ÿ® ÿ£ŸÜ ŸäŸÉŸàŸÜ ÿ®ŸäŸÜ -180 Ÿà 180");
      return;
    }

    setError(null);
    const location: LocationData = {
      lat,
      lng,
      name: `${lat.toFixed(4)}, ${lng.toFixed(4)}`
    };

    onLocationSelect(location);
  };

  if (coordinatesMode) {
    return (
      <Card>
        <CardContent className="p-6">
          <div className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <div className="text-right">
                <Label htmlFor="latitude">ÿÆÿ∑ ÿßŸÑÿπÿ±ÿ∂ (Latitude)</Label>
                <Input
                  id="latitude"
                  type="number"
                  step="any"
                  placeholder="ŸÖÿ´ÿßŸÑ: 24.7136"
                  value={latitude}
                  onChange={(e) => setLatitude(e.target.value)}
                  className="text-right"
                />
              </div>
              <div className="text-right">
                <Label htmlFor="longitude">ÿÆÿ∑ ÿßŸÑÿ∑ŸàŸÑ (Longitude)</Label>
                <Input
                  id="longitude"
                  type="number"
                  step="any"
                  placeholder="ŸÖÿ´ÿßŸÑ: 46.6753"
                  value={longitude}
                  onChange={(e) => setLongitude(e.target.value)}
                  className="text-right"
                />
              </div>
            </div>

            {error && (
              <Alert className="border-red-200 bg-red-50">
                <AlertDescription className="text-right text-red-700">
                  {error}
                </AlertDescription>
              </Alert>
            )}

            <Button 
              onClick={handleCoordinatesSubmit}
              className="w-full"
              disabled={!latitude || !longitude}
            >
              ÿ™ÿ≠ÿØŸäÿØ ÿßŸÑŸÖŸàŸÇÿπ
            </Button>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardContent className="p-6">
        <div className="space-y-4">
          <div className="text-right">
            <Label htmlFor="search">ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÉÿßŸÜ ÿ£Ÿà ÿßŸÑŸÖÿØŸäŸÜÿ©</Label>
            <Input
              id="search"
              type="text"
              placeholder="ŸÖÿ´ÿßŸÑ: ÿßŸÑÿ±Ÿäÿßÿ∂ÿå ÿ¨ÿØÿ©ÿå ÿßŸÑÿØŸÖÿßŸÖ..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="text-right"
              onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
            />
          </div>

          {error && (
            <Alert className="border-red-200 bg-red-50">
              <AlertDescription className="text-right text-red-700">
                {error}
              </AlertDescription>
            </Alert>
          )}

          <Button 
            onClick={handleSearch}
            className="w-full"
            disabled={loading || !searchQuery.trim()}
          >
            {loading ? (
              <div className="flex items-center justify-center">
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ®ÿ≠ÿ´...
              </div>
            ) : (
              "ÿßŸÑÿ®ÿ≠ÿ´ ÿπŸÜ ÿßŸÑŸÖŸàŸÇÿπ"
            )}
          </Button>

          <div className="text-center text-sm text-gray-500">
            <p>ÿ£ŸÖÿ´ŸÑÿ©: ÿßŸÑÿ±Ÿäÿßÿ∂ÿå ŸÖŸÉÿ© ÿßŸÑŸÖŸÉÿ±ŸÖÿ©ÿå ÿßŸÑŸÖÿØŸäŸÜÿ© ÿßŸÑŸÖŸÜŸàÿ±ÿ©ÿå ÿßŸÑÿØŸÖÿßŸÖ</p>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
