"use client";

import { useState, useEffect } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface LocationData {
  lat: number;
  lng: number;
  name?: string;
  polygon?: Array<[number, number]>;
}

interface MapInputProps {
  onLocationSelect: (location: LocationData) => void;
}

export default function MapInput({ onLocationSelect }: MapInputProps) {
  const [selectedPoint, setSelectedPoint] = useState<[number, number] | null>(null);
  const [polygon, setPolygon] = useState<Array<[number, number]>>([]);
  const [drawingMode, setDrawingMode] = useState<'point' | 'polygon'>('point');
  const [mapReady, setMapReady] = useState(false);

  useEffect(() => {
    // ÿ™ÿ≠ŸÖŸäŸÑ Leaflet ŸÅŸÇÿ∑ ŸÅŸä ÿßŸÑŸÖÿ™ÿµŸÅÿ≠
    if (typeof window !== 'undefined') {
      import('leaflet').then((L) => {
        // ÿ•ÿπÿØÿßÿØ ÿ£ŸäŸÇŸàŸÜÿßÿ™ Leaflet ÿßŸÑÿßŸÅÿ™ÿ±ÿßÿ∂Ÿäÿ©
        delete (L.Icon.Default.prototype as any)._getIconUrl;
        L.Icon.Default.mergeOptions({
          iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon-2x.png',
          iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png',
          shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png',
        });
        setMapReady(true);
      });
    }
  }, []);

  const handleMapClick = (lat: number, lng: number) => {
    if (drawingMode === 'point') {
      setSelectedPoint([lat, lng]);
      setPolygon([]);
    } else {
      // ÿ•ÿ∂ÿßŸÅÿ© ŸÜŸÇÿ∑ÿ© ŸÑŸÑŸÖÿ∂ŸÑÿπ
      setPolygon(prev => [...prev, [lat, lng]]);
    }
  };

  const handleConfirmSelection = () => {
    if (drawingMode === 'point' && selectedPoint) {
      onLocationSelect({
        lat: selectedPoint[0],
        lng: selectedPoint[1],
        name: `${selectedPoint[0].toFixed(4)}, ${selectedPoint[1].toFixed(4)}`
      });
    } else if (drawingMode === 'polygon' && polygon.length >= 3) {
      // ÿ≠ÿ≥ÿßÿ® ŸÖÿ±ŸÉÿ≤ ÿßŸÑŸÖÿ∂ŸÑÿπ
      const centerLat = polygon.reduce((sum, point) => sum + point[0], 0) / polygon.length;
      const centerLng = polygon.reduce((sum, point) => sum + point[1], 0) / polygon.length;
      
      onLocationSelect({
        lat: centerLat,
        lng: centerLng,
        name: `ŸÖÿ∂ŸÑÿπ (${polygon.length} ŸÜŸÇÿßÿ∑)`,
        polygon: polygon
      });
    }
  };

  const clearSelection = () => {
    setSelectedPoint(null);
    setPolygon([]);
  };

  const finishPolygon = () => {
    if (polygon.length >= 3) {
      handleConfirmSelection();
    }
  };

  // ÿÆÿ±Ÿäÿ∑ÿ© ÿ®ÿ≥Ÿäÿ∑ÿ© ÿ®ÿßÿ≥ÿ™ÿÆÿØÿßŸÖ iframe ŸÉÿ®ÿØŸäŸÑ ŸÖÿ§ŸÇÿ™
  const SimpleMap = () => {
    const [clickCoords, setClickCoords] = useState<string>("");

    return (
      <div className="space-y-4">
        <div className="h-96 w-full rounded-lg overflow-hidden border bg-gray-100 flex items-center justify-center">
          <div className="text-center p-8">
            <div className="text-6xl mb-4">üó∫Ô∏è</div>
            <h3 className="text-lg font-semibold mb-2">ÿÆÿ±Ÿäÿ∑ÿ© ÿ™ŸÅÿßÿπŸÑŸäÿ©</h3>
            <p className="text-gray-600 mb-4">
              {drawingMode === 'point' 
                ? "ÿßÿ≥ÿ™ÿÆÿØŸÖ ÿßŸÑÿ≠ŸÇŸàŸÑ ÿ£ÿØŸÜÿßŸá ŸÑÿ•ÿØÿÆÿßŸÑ ÿßŸÑÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™"
                : "ÿßÿ≥ÿ™ÿÆÿØŸÖ ÿßŸÑÿ≠ŸÇŸàŸÑ ÿ£ÿØŸÜÿßŸá ŸÑÿ•ÿØÿÆÿßŸÑ ŸÜŸÇÿßÿ∑ ÿßŸÑŸÖÿ∂ŸÑÿπ"
              }
            </p>
            
            {/* ÿ≠ŸÇŸàŸÑ ÿ•ÿØÿÆÿßŸÑ ÿßŸÑÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ */}
            <div className="max-w-md mx-auto space-y-3">
              <div className="grid grid-cols-2 gap-2">
                <input
                  type="number"
                  step="any"
                  placeholder="ÿÆÿ∑ ÿßŸÑÿπÿ±ÿ∂"
                  className="px-3 py-2 border rounded text-center"
                  onChange={(e) => {
                    const lat = parseFloat(e.target.value);
                    const lng = selectedPoint ? selectedPoint[1] : 46.6753;
                    if (!isNaN(lat)) {
                      handleMapClick(lat, lng);
                    }
                  }}
                />
                <input
                  type="number"
                  step="any"
                  placeholder="ÿÆÿ∑ ÿßŸÑÿ∑ŸàŸÑ"
                  className="px-3 py-2 border rounded text-center"
                  onChange={(e) => {
                    const lng = parseFloat(e.target.value);
                    const lat = selectedPoint ? selectedPoint[0] : 24.7136;
                    if (!isNaN(lng)) {
                      handleMapClick(lat, lng);
                    }
                  }}
                />
              </div>
              
              {drawingMode === 'polygon' && (
                <Button
                  size="sm"
                  onClick={() => {
                    const lat = Math.random() * 10 + 20; // ÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ ÿπÿ¥Ÿàÿßÿ¶Ÿäÿ© ŸÑŸÑÿ™ÿ¨ÿ±ÿ®ÿ©
                    const lng = Math.random() * 20 + 35;
                    handleMapClick(lat, lng);
                  }}
                >
                  ÿ•ÿ∂ÿßŸÅÿ© ŸÜŸÇÿ∑ÿ© ÿπÿ¥Ÿàÿßÿ¶Ÿäÿ© ŸÑŸÑÿ™ÿ¨ÿ±ÿ®ÿ©
                </Button>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  };

  return (
    <Card>
      <CardContent className="p-6">
        <div className="space-y-4">
          {/* ÿ£ÿ≤ÿ±ÿßÿ± ÿßŸÑÿ™ÿ≠ŸÉŸÖ */}
          <div className="flex justify-center space-x-4 mb-4">
            <Button
              variant={drawingMode === 'point' ? 'default' : 'outline'}
              onClick={() => {
                setDrawingMode('point');
                clearSelection();
              }}
            >
              ÿ™ÿ≠ÿØŸäÿØ ŸÜŸÇÿ∑ÿ©
            </Button>
            <Button
              variant={drawingMode === 'polygon' ? 'default' : 'outline'}
              onClick={() => {
                setDrawingMode('polygon');
                clearSelection();
              }}
            >
              ÿ±ÿ≥ŸÖ ŸÖÿ∂ŸÑÿπ
            </Button>
            <Button variant="outline" onClick={clearSelection}>
              ŸÖÿ≥ÿ≠ ÿßŸÑÿ™ÿ≠ÿØŸäÿØ
            </Button>
          </div>

          {/* ÿ™ÿπŸÑŸäŸÖÿßÿ™ */}
          <Alert>
            <AlertDescription className="text-right">
              {drawingMode === 'point' 
                ? "ÿ£ÿØÿÆŸÑ ÿßŸÑÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ ŸÅŸä ÿßŸÑÿ≠ŸÇŸàŸÑ ÿ£ÿØŸÜÿßŸá ŸÑÿ™ÿ≠ÿØŸäÿØ ŸÜŸÇÿ∑ÿ©"
                : "ÿ£ÿØÿÆŸÑ ÿ•ÿ≠ÿØÿßÿ´Ÿäÿßÿ™ ŸÖÿ™ÿπÿØÿØÿ© ŸÑÿ•ŸÜÿ¥ÿßÿ° ŸÖÿ∂ŸÑÿπ (3 ŸÜŸÇÿßÿ∑ ÿπŸÑŸâ ÿßŸÑÿ£ŸÇŸÑ)"
              }
            </AlertDescription>
          </Alert>

          {/* ÿßŸÑÿÆÿ±Ÿäÿ∑ÿ© */}
          <SimpleMap />

          {/* ŸÖÿπŸÑŸàŸÖÿßÿ™ ÿßŸÑÿ™ÿ≠ÿØŸäÿØ */}
          {drawingMode === 'point' && selectedPoint && (
            <div className="text-right p-3 bg-blue-50 rounded">
              <p className="text-sm text-blue-700">
                ÿßŸÑŸÜŸÇÿ∑ÿ© ÿßŸÑŸÖÿ≠ÿØÿØÿ©: {selectedPoint[0].toFixed(4)}, {selectedPoint[1].toFixed(4)}
              </p>
            </div>
          )}

          {drawingMode === 'polygon' && polygon.length > 0 && (
            <div className="text-right p-3 bg-green-50 rounded">
              <p className="text-sm text-green-700">
                ÿπÿØÿØ ÿßŸÑŸÜŸÇÿßÿ∑: {polygon.length}
                {polygon.length >= 3 && " (ŸäŸÖŸÉŸÜ ÿ•ŸÜŸáÿßÿ° ÿßŸÑŸÖÿ∂ŸÑÿπ)"}
              </p>
              <div className="mt-2 text-xs">
                {polygon.map((point, index) => (
                  <div key={index}>
                    ŸÜŸÇÿ∑ÿ© {index + 1}: {point[0].toFixed(4)}, {point[1].toFixed(4)}
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* ÿ£ÿ≤ÿ±ÿßÿ± ÿßŸÑÿ™ÿ£ŸÉŸäÿØ */}
          <div className="flex justify-center space-x-4">
            {drawingMode === 'point' && selectedPoint && (
              <Button onClick={handleConfirmSelection}>
                ÿ™ÿ£ŸÉŸäÿØ ÿ™ÿ≠ÿØŸäÿØ ÿßŸÑŸÜŸÇÿ∑ÿ©
              </Button>
            )}
            
            {drawingMode === 'polygon' && polygon.length >= 3 && (
              <Button onClick={finishPolygon}>
                ÿ•ŸÜŸáÿßÿ° ÿ±ÿ≥ŸÖ ÿßŸÑŸÖÿ∂ŸÑÿπ
              </Button>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
